@layer variables {
  :root {
    --clr-primary-dk: #ba4270;
    --clr-primary-lt: #fbfcfe;
    --clr-secondary-dk: #36536b;
    --clr-secondary-dkr: #1b262f;
    --clr-secondary-lt: #da6d97;
    --clr-secondary-ntrl: #6c8294;
    --clr-body--background: #edf3f8;
    --clr-border-ltr: #97979740;
    --clr-border-lt: #97979780;
    --clr-error-lt: #ff000080;
    --clr-error: red;
    --ff-base: "Public Sans", sans-serif;
    --ff-alt: "DM Serif Display", serif;
    --fs-h1-lrg: 7.2rem;
    --fs-h1-sml: 5.6rem;
    --fs-h2: 4.8rem;
    --fs-h3-lrg: 3.2rem;
    --fs-h3-sml: 1.8rem;
    --fs-h4: 2.4rem;
    --fs-body: 1.5rem;
    --fs-body-lrg: 2rem;
    --lh-h1-lrg: 7.2rem;
    --lh-h1-sml: 5.6rem;
    --lh-h2: 5.6rem;
    --lh-h3-lrg: 4rem;
    --lh-h3-sml: 2.5rem;
    --lh-h4: 3.2rem;
    --lh-body: 2.8rem;
    --ls-h1-lrg: -.55px;
    --ls-h1-sml: -.43px;
    --ls-h2: -.37px;
    --ls-h3-lrg: -.25px;
    --ls-h3-sml: -.14px;
    --ls-h4: -.18px;
    --ls-btn: -.12px;
    --br-stnd: 2.4rem;
    --ad-standard: .2s;
    --pd-stnd: 1.5rem 2.5rem;
    --cntr-size: 110rem;
    --cntr-pad: 3rem;
    --cntr-pad--mob: 4rem;
    --bp-desktop-lg: 90em;
  }
}

@layer base {
  *, :before, :after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  :focus-visible {
    border-radius: 6px;
    outline: none;
    transition: box-shadow .2s ease-in-out;
    box-shadow: 0 0 0 4px #007bff4d, 0 0 0 2px #007bff80;
  }

  html {
    font-size: 62.5%;
  }

  body, html {
    overflow-x: hidden;
  }

  a:link, a:visited {
    color: inherit;
    text-decoration: none;
  }

  body {
    color: var(--clr-secondary-dk);
    font-family: var(--ff-base);
    background-color: var(--clr-body--background);
  }

  fieldset {
    border: none;
  }
}

@layer components {
  .heading--primary--lrg {
    font-size: var(--fs-h1-lrg);
    line-height: var(--lh-h1-lrg);
    font-family: var(--ff-alt);
    letter-spacing: var(--ls-h1-lrg);
    font-weight: 400;
  }

  @media only screen and (width <= 68.75em) {
    .heading--primary--lrg {
      font-size: 6rem;
    }
  }

  @media only screen and (width <= 60em) {
    .heading--primary--lrg {
      font-size: var(--fs-h2);
      line-height: var(--lh-h2);
      letter-spacing: var(--ls-h2);
    }
  }

  @media only screen and (width <= 28em) {
    .heading--primary--lrg {
      font-size: var(--fs-h3-lrg);
      line-height: var(--lh-h3-lrg);
      letter-spacing: var(--ls-h3-lrg);
    }
  }

  .heading--primary--sml {
    font-size: var(--fs-h1-sml);
    line-height: var(--lh-h1-sml);
    font-family: var(--ff-alt);
    letter-spacing: var(--ls-h1-sml);
    font-weight: 400;
  }

  @media only screen and (width <= 60em) {
    .heading--primary--sml {
      font-size: var(--fs-h2);
      line-height: var(--lh-h2);
      letter-spacing: var(--ls-h2);
    }
  }

  @media only screen and (width <= 41em) {
    .heading--primary--sml {
      font-size: var(--fs-h3-lrg);
      line-height: var(--lh-h3-lrg);
      letter-spacing: var(--ls-h3-lrg);
    }
  }

  .heading--secondary {
    font-size: var(--fs-h2);
    line-height: var(--lh-h2);
    font-family: var(--ff-alt);
    letter-spacing: var(--ls-h2);
    font-weight: 400;
  }

  @media only screen and (width <= 28em) {
    .heading--secondary {
      font-size: var(--fs-h3-lrg);
      line-height: var(--lh-h3-lrg);
      letter-spacing: var(--ls-h3-lrg);
    }
  }

  .heading--tertiary--lrg {
    font-size: var(--fs-h3-lrg);
    line-height: var(--lh-h3-lrg);
    font-family: var(--ff-alt);
    letter-spacing: var(--ls-h3-lrg);
    font-weight: 400;
  }

  @media only screen and (width <= 60em) {
    .heading--tertiary--lrg {
      font-size: var(--fs-h4);
      line-height: var(--lh-h4);
      letter-spacing: var(--ls-h4);
    }
  }

  .heading--tertiary--sml {
    font-size: var(--fs-h3-sml);
    line-height: var(--lh-h3-sml);
    font-family: var(--ff-base);
    letter-spacing: var(--ls-h3-sml);
    font-weight: 700;
  }

  .heading--quaternary {
    font-size: var(--fs-h4);
    line-height: var(--lh-h4);
    font-weight: 400;
    font-family: var(--ff-alt);
    letter-spacing: var(--ls-h4);
  }

  .paragraph {
    font-size: var(--fs-body);
    line-height: var(--lh-body);
    font-family: var(--ff-base);
    color: var(--clr-secondary-ntrl);
  }

  .btn, .btn:link, .btn:visited {
    cursor: pointer;
    border-radius: var(--br-stnd);
    transition: var(--ad-standard) all;
    text-align: center;
    padding: var(--pd-stnd);
    font-family: inherit;
    font-size: var(--fs-body);
    letter-spacing: var(--ls-btn);
    font-weight: 700;
    text-decoration: none;
    display: inline-block;
  }

  .btn--primary, .btn--primary:link, .btn--primary:active {
    color: var(--clr-primary-lt);
    background-color: var(--clr-primary-dk);
    border: none;
  }

  :is(.btn--primary, .btn--primary:link, .btn--primary:active):hover {
    background-color: var(--clr-secondary-lt);
  }

  .btn--secondary, .btn--secondary:link, .btn--secondary:visited {
    color: var(--clr-primary-lt);
    border: 1px solid var(--clr-primary-lt);
    background-color: #0000;
  }

  :is(.btn--secondary, .btn--secondary:link, .btn--secondary:visited):hover {
    color: var(--clr-secondary-dkr);
    background-color: #fff;
  }

  .btn--form {
    color: var(--clr-secondary-dk);
    border: 1px solid var(--clr-secondary-dk);
    background-color: #0000;
  }

  .btn--form:hover {
    color: #fff;
    background-color: var(--clr-secondary-dk);
  }

  .input {
    color: var(--clr-secondary-dk);
    padding: var(--pd-stnd);
    border-radius: var(--br-stnd);
    border: none;
    width: 100%;
    max-width: 44.5rem;
    font-family: inherit;
    font-weight: 700;
  }

  .input::placeholder {
    opacity: .5;
    font-size: var(--fs-body);
  }

  .input--form {
    border: none;
    border-bottom: 1px solid var(--clr-border-lt);
    color: var(--clr-secondary-dk);
    background-color: #0000;
    padding: 2rem;
    font-family: inherit;
  }

  .input--form:is(textarea) {
    resize: none;
  }

  .input--form::placeholder {
    opacity: .7;
    font-size: var(--fs-body);
  }

  :is(.input-error, .input--email-error) {
    position: relative;
  }

  :is(.input-error, .input--email-error) :is(input, textarea) {
    border-bottom: 1px solid var(--clr-error);
    color: var(--clr-error-lt);
    position: relative;
  }

  :is(.input-error, .input--email-error) :is(input, textarea)::placeholder {
    color: var(--clr-error);
  }

  :is(.input-error, .input--email-error):after {
    content: "This field canâ€™t be empty";
    color: var(--clr-error-lt);
    width: 100%;
    font-size: 1.1rem;
    font-weight: 700;
    position: absolute;
    bottom: -2rem;
    left: 2rem;
  }

  :is(.input-error, .input--email-error).input--email-error:after {
    content: "Specify a valid email";
  }

  .navigation {
    flex: 1;
    display: flex;
  }

  .navigation__list {
    flex: 1;
    align-items: center;
    gap: 4rem;
    list-style: none;
    display: flex;
  }

  .navigation__item--footer, .navigation__item:not(:has(.btn)) {
    font-size: var(--fs-body);
    opacity: .7;
    font-weight: 700;
    transition: all .2s;
  }

  :is(.navigation__item--footer, .navigation__item:not(:has(.btn))):hover {
    opacity: 1;
  }

  .navigation__item--footer {
    color: var(--clr-primary-lt);
  }

  .navigation__logo {
    width: 13.5rem;
    height: 3.8rem;
  }

  .navigation__item:has(.btn) {
    margin-left: auto;
  }

  .navigation__social-container {
    gap: 2.5rem;
  }

  .navigation__social-link:hover path {
    fill: var(--clr-secondary-lt);
    transition: all .2s;
  }

  .v-hidden {
    clip: rect(0, 0, 0, 0);
    border: 0;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }

  .text--white {
    color: #fff;
  }

  .opacity-70 {
    opacity: .7;
  }

  .img {
    width: 100%;
    display: inline-block;
  }

  .no-scroll {
    overflow: hidden;
  }

  .circle-shape {
    opacity: .3;
    z-index: -1;
    pointer-events: none;
    background-color: #979797;
    border-radius: 50%;
    width: 90rem;
    height: 90rem;
    position: absolute;
    overflow: hidden;
  }
}

@layer layout {
  .grid-col--2 {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    display: grid;
  }

  @media only screen and (width <= 60em) {
    .grid-col--2 {
      grid-template-columns: 1fr;
    }
  }

  .grid-col--3 {
    grid-template-columns: repeat(3, 1fr);
    display: grid;
  }

  .grid-center {
    place-items: center;
  }

  .container {
    max-width: calc(110rem + (2 * var(--cntr-pad)));
    padding: 0 var(--cntr-pad);
    margin: 0 auto;
  }

  @media only screen and (width <= 28em) {
    .container {
      max-width: calc(110rem + (2 * var(--cntr-pad--mob)));
      padding: 0 var(--cntr-pad--mob);
    }
  }

  .container--sml {
    max-width: calc(92rem + (2 * var(--cntr-pad)));
    padding: 0 var(--cntr-pad);
    margin: 0 auto;
  }

  @media only screen and (width <= 28em) {
    .container--sml {
      max-width: calc(92rem + (2 * var(--cntr-pad--mob)));
      padding: 0 var(--cntr-pad--mob);
    }
  }

  .grid-center {
    align-items: center;
  }

  .flex {
    display: flex;
  }

  .flex-sb {
    justify-content: space-between;
  }

  .flex-column {
    flex-direction: column;
    display: flex;
  }

  .flex-center {
    justify-content: center;
    align-items: center;
  }

  .flex-ai-center {
    align-items: center;
  }
}

@layer header {
  .header-container {
    align-items: center;
    gap: 6.5rem;
    padding-top: 4.5rem;
    display: flex;
  }

  @media only screen and (width <= 41em) {
    .header-container {
      justify-content: space-between;
      position: relative;
    }

    .navigation--header {
      transition: transform .3s;
      display: none;
    }

    @starting-style {
      .navigation--header {
        transform: translateX(100%);
      }
    }
  }

  .nav--active .navigation--header {
    display: block;
  }

  .nav__icon--close {
    display: none;
    transform: translateX(100%);
  }

  .nav--active .nav__icon--close {
    display: block;
  }

  .nav--active .nav__icon--open {
    display: none;
  }

  @media only screen and (width <= 41em) {
    .nav--active .navigation--header {
      background-color: var(--clr-secondary-dkr);
      z-index: 1000;
      width: 80%;
      height: 100vh;
      padding-top: 10rem;
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  .nav--active .navigation__item:has(.btn) {
    width: 90%;
  }

  .nav--active .navigation__item:has(.btn) .btn {
    width: 100%;
  }

  .navigation__button {
    cursor: pointer;
    z-index: 9999;
    background: none;
    border: none;
    outline: none;
    display: none;
  }

  @media only screen and (width <= 41em) {
    .navigation__button {
      display: block;
    }

    .navigation__list--header {
      flex-direction: column;
      align-items: center;
      padding: 4rem 2.4rem;
      position: relative;
    }

    .navigation__list--header .navigation__item {
      font-size: var(--fs-body-lrg);
      color: var(--clr-primary-lt);
      font-weight: 700;
    }

    .navigation__list--header .navigation__item:has(.btn) {
      margin-left: 0;
    }

    .navigation__list--header:before {
      content: "";
      background-color: var(--clr-primary-lt);
      opacity: .15;
      width: 80%;
      height: 1px;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%);
    }
  }
}

@layer hero {
  .hero {
    margin-top: 9.5rem;
    margin-bottom: 7.5rem;
    position: relative;
  }

  @media only screen and (width <= 60em) {
    .hero {
      margin-top: 4.8rem;
    }

    .hero__container {
      gap: 4.8rem;
    }
  }

  .hero__img-box {
    justify-content: flex-end;
    display: flex;
  }

  @media only screen and (width <= 60em) {
    .hero__img-box {
      grid-row: 1;
    }
  }

  @media only screen and (width <= 28em) {
    .hero__img-box {
      width: 40%;
    }
  }

  @media only screen and (width <= 60em) {
    .hero__text-content {
      text-align: center;
      flex-direction: column;
      align-items: center;
      max-width: 57rem;
      display: flex;
    }
  }

  @media only screen and (width <= 28em) {
    .hero__text-content {
      max-width: 33rem;
    }
  }

  .hero__img {
    width: 100%;
    max-width: 27rem;
  }

  .cta__form {
    width: 100%;
    max-width: 44.5rem;
    position: relative;
  }

  @media only screen and (width <= 28em) {
    .cta__form {
      flex-direction: column;
      gap: 1.5rem;
      display: flex;
    }
  }

  .cta__btn {
    position: absolute;
    right: 0;
  }

  @media only screen and (width <= 28em) {
    .cta__btn {
      position: static;
    }
  }

  .hero__heading {
    margin-bottom: 5rem;
  }

  @media only screen and (width <= 28em) {
    .hero__heading {
      margin-bottom: 2.4rem;
    }
  }

  .hero__paragraph {
    margin-top: 1.5rem;
    padding-left: 2.5rem;
  }

  @media only screen and (width <= 60em) {
    .hero__paragraph {
      padding-left: 0;
    }
  }

  @media only screen and (width <= 28em) {
    .hero__paragraph {
      margin-top: 2.4rem;
    }
  }

  .hero__shape {
    top: -43rem;
    right: -10rem;
  }

  @media only screen and (width <= 90em) {
    .hero__shape {
      top: -53rem;
      right: -20rem;
    }
  }

  @media only screen and (width <= 68.75em) {
    .hero__shape {
      top: -73rem;
      right: -30rem;
    }
  }

  @media only screen and (width <= 60em) {
    .hero__shape {
      top: 50%;
      left: 50%;
      right: 0;
      transform: translate(-50%, -120%);
    }
  }
}

@layer partners {
  .partners {
    background-color: var(--clr-secondary-dkr);
    margin-bottom: 15rem;
    padding: 10rem 0;
    position: relative;
    overflow: hidden;
  }

  @media only screen and (width <= 60em) {
    .partners {
      margin-bottom: 10rem;
      padding: 8.5rem 0;
    }
  }

  .partners__heading {
    margin-bottom: 2.4rem;
  }

  @media only screen and (width <= 28em) {
    .partners__heading {
      margin-bottom: 1.5rem;
    }
  }

  .partners__paragraph {
    margin-bottom: 3.2rem;
  }

  @media only screen and (width <= 28em) {
    .partners__paragraph {
      margin-bottom: 6rem;
    }
  }

  .partners__logo {
    pointer-events: none;
    width: 100%;
  }

  .partners__container {
    gap: 13rem;
    display: grid;
  }

  @media only screen and (width <= 60em) {
    .partners__container {
      gap: 6.5rem;
    }
  }

  @media only screen and (width <= 28em) {
    .partners__container {
      gap: 5.5rem;
    }
  }

  @media only screen and (width <= 60em) {
    .partners__text-content {
      text-align: center;
      max-width: 45.7rem;
    }
  }

  .partners__shape {
    z-index: 1;
    opacity: .1;
    top: -42rem;
    left: -22rem;
  }

  @media only screen and (width <= 90em) {
    .partners__shape {
      top: -50rem;
      left: -30rem;
    }
  }

  @media only screen and (width <= 60em) {
    .partners__shape {
      top: 50%;
      left: 50%;
      right: 0;
      transform: translate(-50%, -110%);
    }
  }

  .partners__list {
    gap: 4rem 6rem;
  }

  @media only screen and (width <= 60em) {
    .partners__list {
      grid-row: 1;
    }
  }

  @media only screen and (width <= 28em) {
    .partners__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  .partners__img {
    max-width: 15rem;
  }
}

@layer features {
  .features {
    margin-bottom: 10rem;
  }

  @media only screen and (width <= 41em) {
    .features {
      margin-bottom: 8rem;
    }
  }

  .features__heading {
    margin-bottom: 2.4rem;
  }

  .features__container {
    flex-direction: column;
    gap: 15rem;
    margin-bottom: 15rem;
    display: flex;
  }

  @media only screen and (width <= 60em) {
    .features__container {
      gap: 10rem;
      margin-bottom: 10rem;
    }
  }

  @media only screen and (width <= 28em) {
    .features__container {
      gap: 8rem;
      margin-bottom: 8rem;
    }
  }

  .features__wrapper {
    align-items: center;
    gap: 12.5rem;
    display: flex;
  }

  @media only screen and (width <= 60em) {
    .features__wrapper {
      flex-direction: column;
      gap: 5rem;
    }

    .features__wrapper:nth-of-type(2n) .features__img-content {
      order: -1;
    }
  }

  .features__img-content--2 {
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    display: grid;
    position: relative;
  }

  @media only screen and (width <= 60em) {
    .features__img-content--2 {
      grid-row: 3;
    }
  }

  .features__img-content {
    flex: 1;
  }

  @media only screen and (width <= 60em) {
    .features__img-content {
      max-width: 45rem;
    }
  }

  .features__text-content {
    flex: 1;
  }

  @media only screen and (width <= 60em) {
    .features__text-content {
      text-align: center;
      max-width: 57.3rem;
    }
  }

  .features__feature-img {
    width: 10.5rem;
  }

  @media only screen and (width <= 60em) {
    .features__feature-img {
      width: 7.5rem;
    }
  }

  @media only screen and (width <= 41em) {
    .features__feature-img {
      width: 10.5rem;
    }
  }

  .features__list {
    gap: 3rem;
  }

  @media only screen and (width <= 60em) {
    .features__list {
      gap: 1rem;
    }
  }

  @media only screen and (width <= 41em) {
    .features__list {
      grid-template-columns: 1fr;
      justify-items: center;
      gap: 4.8rem;
    }
  }

  .features__feature-header {
    gap: 3.2rem;
  }

  .features__feature {
    text-align: center;
    gap: 1.5rem;
  }

  @media only screen and (width <= 41em) {
    .features__feature {
      max-width: 35rem;
    }
  }

  .features__shape {
    top: -25rem;
    right: -75rem;
  }

  @media only screen and (width <= 60em) {
    .features__shape {
      display: none;
    }
  }
}

@layer cta {
  .cta {
    margin-bottom: 10rem;
  }

  .cta__container {
    justify-content: space-between;
  }

  @media only screen and (width <= 60em) {
    .cta__container {
      flex-direction: column;
      gap: 4rem;
    }
  }

  @media only screen and (width <= 28em) {
    .cta__container {
      gap: 3rem;
    }
  }
}

@layer footer {
  .footer {
    background-color: var(--clr-secondary-dkr);
    padding: 3rem 0;
    position: relative;
    overflow: hidden;
  }

  .footer__container {
    gap: 6.5rem;
  }

  @media only screen and (width <= 41em) {
    .footer__container {
      flex-direction: column;
      gap: 4.5rem;
      padding: 4rem 0 5.5rem;
    }

    .navigation__list--footer {
      flex-direction: column;
      gap: 3rem;
    }

    .navigation--footer {
      flex-direction: column;
      gap: 4rem;
    }
  }

  .footer__shape {
    z-index: 10;
    opacity: .1;
    top: -10rem;
    right: -20rem;
  }

  @media only screen and (width <= 60em) {
    .footer__shape {
      top: -10rem;
      right: -50rem;
    }
  }

